:root {
  --red-orange: #f87070;
  --sky-blue: #70f3f8;
  --purple: #d881f8;
  --light-sky-blue: #d7e0ff;
  --dark-blue: #1e213f;
  --write1: #ffffff;
  --write2: #eff1fa;
  --darker-blue: #161932;
  --write3: #e3e1e1;
}

body {
  background-color: var(--dark-blue);
  color: var(--write3);
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-content: center;
  align-items: center;
  margin: 0;
}

#root {
  width: 100%;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4em;
  padding: 0 2em;
}
.switch {
  display: flex;
  height: 5em;
  width: 100%;
  border-radius: 50px;
  justify-content: space-around;
  align-items: center;
  font-size: 0.6em;
  background-color: var(--darker-blue);
  box-shadow: 0 0 4px var(--purple);
}

.display {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
}

.border {
  background-color: var(--darker-blue);
  border-radius: 50%;
  display: flex;
  width: 90%;
  height: 90%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: background .3s;
}
.switch-blue{
  box-shadow: 0 0 4px var(--sky-blue) ;
}
.switch-red{
  box-shadow: 0 0 4px var(--red-orange) ;
}
.bk-radius {
  height: 300px;
  width: 300px;
  background: linear-gradient(-45deg, #2e325a, #0e112a);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0 4px var(--purple);

}

.border:hover{
  background: var(--dark-blue);

}


@keyframes bkra3 {
  from { box-shadow: 0 0 30px var(--purple);}
  to { box-shadow: 0 0 0 var(--purple);}
}
@keyframes bkra2 {
  from { box-shadow: 0 0 30px var(--sky-blue)}
  to { box-shadow: 0 0 0 var(--sky-blue)}
}
@keyframes bkra1 {
  from { box-shadow: 0 0 30px var(--red-orange)}
  to { box-shadow: 0 0 0 var(--red-orange)}
}
.time {
  font-size: 4em;
  
}


@keyframes timechange{
  0% {transform: scale(1.2);}
  100% {transform: scale(0.8);}
}

.setting-icon {
  padding: 1%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  border-radius: 50%;
  transition: box-shadow .2s;
}

.setting-icon:hover{
  box-shadow: 0 0 10px white;
}

.switch div {
  width: 29%;
  height: 80%;
  font-size: 1.2em;
  text-align: center;
  font-weight: bold;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .3s;
}



.wrapper {
  width: 100%; 
  height: 100%;
  position: absolute;
  background: #000000a6;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--darker-blue);
  
}

.setting {
  background: white;
  border-radius: 18px;
  justify-content: center;
  width: 95%;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: showup .5s;
}
@keyframes showup {
  from{margin-top: 10%;}
  to{padding:0;}
}



.set-wrapper{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.set-time, .font,.color{
  width: 85%;
  padding-bottom: 3vh;
}
.set-time, .font{
  border-bottom: .063rem solid #e3e1e1;
}


.set-wrapper input{
  width: 50%;
  background: #e0e0e0;
  border: none;
  height: 33px;
  border-radius: 7px;
  font-weight: 800;
  padding: 0 10px;
}

.head{
  border-bottom: .063rem solid #e3e1e1;
  width: 85%;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.select{
  background: var(--darker-blue) !important;
  color: white;
}
.round-rapper{
  display: flex;
  justify-content: space-evenly;
  align-items: center;

}

.round{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #e0e0e0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bolder;
}

.roboto{
  font-family: 'Roboto', sans-serif;
}
.festive{
  font-family: 'Festive', cursive;
}
.Abril{
  font-family: 'Abril Fatface', cursive;
}

.red{
  background: var(--red-orange);
}
.blue{
  background: var(--sky-blue);
}
.purple{
  background: var(--purple);
}

.btn{
  background: var(--red-orange);
  color: white;
  font-size: 1.5em;
  font-weight: bolder;
  width: 40%;
  border-radius: 50px;
  text-align: center;
  height: 50px;
  position: relative;
  top: 25px;
}

@media screen and (min-width: 750px) {
  .switch {
    max-width: 600px;
  }
  .setting{
    max-width: 600px;
  }
}

@media screen and (max-width: 370px) {
  .bk-radius {
    width: 250px;
    height: 250px;
  }
}
@media screen and (min-width: 768px) {
  .bk-radius {
    width: 500px;
    height: 500px;
  }
  .time{
    font-size: 8em;
  }
  
.wrapper {
 
  height: 110%;
}
}